import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getCustomerCategories, deleteCustomerCategories } from '../../api/sales/customerCategory';

const CustomerCategories = (props) => {
    const [customerCategoriesArr, setCustomerCategoriesArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, customerCategoryId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        const queryObj = new URLSearchParams(props.location.search)
        getCustomerCategories({
            "action": "get",
            "filter": "none",
            "start": parseInt(queryObj.get('start')) || 0, // i don't think start and end is set at there
            "end": (parseInt(queryObj.get('end')) || 25),
        }, controller.signal)
            .then((res) => {
                if (res.json.status) {
                    console.log(res.json)
                    setCustomerCategoriesArr(res.json.customerCategories);
                    setLoading(false);
                    setInstanceCount(res.json.count)
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort();
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, customerCategoryId: id });
    }

    const _delete = (idToBeDeleted) => {
        deleteCustomerCategories({ action: 'delete', customer_category_ids: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedCustomerCategory = customerCategoriesArr.find(item => item.id === idToBeDeleted);
                    let tempArr = customerCategoriesArr;
                    tempArr.splice(tempArr.indexOf(deletedCustomerCategory), 1);
                    await setCustomerCategoriesArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, customerCategoryId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Customer Category deleted successfully!', background: '#dffff0' })
                } else {

                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const CustomerCategoriesList = customerCategoriesArr.map((cat, index) => {
        return (
            <tr key={index}>
                <th scope="row">{cat.id}</th>
                <td>{cat.name}</td>
                <td>{cat.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/customer-category/update/' + cat.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(cat.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Customer Categories</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`customer-category/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {CustomerCategoriesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(customerCategoriesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.customerCategoryId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default CustomerCategories;